'use strict';
var arCon = document.getElementsByClassName('post-body');
for (var i = 0; i < arCon.length; i++) {
    arCon[i].addEventListener('copy', function (e) {
        if (window.getSelection().toString() && window.getSelection().toString().length > 42) {
            setClipboardText(e);
            notie('商业转载请联系作者获得授权\uFF0C非商业转载请注明出处\uFF0C谢谢合作\u3002', {
                type: 'info',
                autoHide: true
            });
        }
    });
}
function setClipboardText(event) {
    var clipboardData = event.clipboardData || window.clipboardData;
    if (clipboardData) {
        event.preventDefault();
        var htmlData = '' + '著作权归作者所有\u3002<br>' + '商业转载请联系作者获得授权\uFF0C非商业转载请注明出处\u3002<br>' + '作者\uFF1A' + CONFIG.site.author + '<br>' + '链接\uFF1A' + window.location.href + '<br>' + '来源\uFF1A' + CONFIG.site.title + '<br><br>' + window.getSelection().toString();
        var textData = '' + '著作权归作者所有\u3002\n' + '商业转载请联系作者获得授权\uFF0C非商业转载请注明出处\u3002\n' + '作者\uFF1A' + CONFIG.site.author + '\n' + '链接\uFF1A' + window.location.href + '\n' + '来源\uFF1A' + CONFIG.site.title + '' + window.getSelection().toString();
        clipboardData.setData('text/html', htmlData);
        clipboardData.setData('text/plain', textData);
    }
}


